import React from 'react';
import { clsx } from 'clsx';
import { HeaderShortProps, HeaderShortComponentProps } from './headershort.interface';

export const HeaderShortComponent = ({ tagline, headline, textColor = 'indigo', showArrow, className }: HeaderShortComponentProps) => {
  const containerClass = {
    '!zep-text-typography-light-100': textColor === 'white',
    '!zep-text-typography-dark-100': textColor === 'black',
    'zep-text-indigo-500': textColor === 'indigo',
  };

  const taglineClass = {
    '!zep-text-steel-500': textColor === 'white',
    'zep-text-steel-500': textColor === 'indigo',
    'zep-text-typography-dark-70 zep-opacity-70': textColor === 'black',
  }

  return (
    <div
      className={clsx(
        'zep-flex',
        'zep-items-center',
        'xl:zep-gap-3.5',
        'md:zep-gap-1.5',
        'sm:zep-gap-2.5',
        'zep-gap-1.5',
        className,
      )}
      data-testid="header-short-component"
    >
      {showArrow ? (
        <img
          className={clsx(
            'zep-h-4',
            'sm:zep-h-5',
            'md:zep-h-4',
            'xl:zep-h-[100px]',
            'zep-w-auto',
          )}
          alt="forward arrow"
          src="./assets/moving-forward-arrow.svg"
        />
      ) : null}
      <div className="zep-block zep-w-full">
        {tagline && (
          <h3
            className={clsx(
              taglineClass,
              'zep-mb-0.5',
              'zep-typography-taglineMD',
              'zep-break-all',
            )}
          >
            {tagline}
          </h3>
        )}
        <h2 className={clsx(containerClass, 'zep-typography-headlineLG-fluid-cqi', 'zep-break-all')}>
          {headline}
        </h2>
      </div>
    </div>
  )
}

export const HeaderShort: React.FC<HeaderShortProps> = (props) => {
  return (
    <div
      className={clsx(
        'zep-container',
        'zep-grid',
        'zep-gap-1',
        'sm:zep-gap-1.5',
        'md:zep-gap-2',
        'zep-grid-cols-4',
        'sm:zep-grid-cols-8',
        'md:zep-grid-cols-16',
        'zep-mx-auto',
      )}
      data-testid="header-short"
    >
      <HeaderShortComponent
        {...props}
        className={clsx(
          'zep-col-span-4',
          'sm:zep-col-span-8',
          'md:zep-col-start-1',
          'zep-px-1',
          'sm:zep-px-1.5',
          'md:zep-px-[0px]',
          {
            'md:zep-col-span-6': props.showArrow,
            'md:zep-col-span-5': !props.showArrow,
          },
        )}
      />
    </div>
  );
};
